@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global CSS Reset for better cross-browser consistency */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*::before,
*::after {
  box-sizing: border-box;
}

@layer base {
  html {
    font-size: 12.8px; /* 16px * 0.8 = 12.8px for 80% scaling effect */
    scroll-behavior: smooth;
    text-size-adjust: 100%; /* Prevents text scaling in landscape mode on mobile */
    -webkit-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
  }
  
  body {
    @apply text-gray-100 font-sans antialiased;
    line-height: 1.6;
    min-height: 100vh;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  /* Improve image rendering */
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  /* Remove default form styling */
  input, button, textarea, select {
    font: inherit;
  }
  
  /* Scrollbar styling */
  ::-webkit-scrollbar {
    width: 0.5rem; /* 8px converted to rem */
  }
  
  ::-webkit-scrollbar-track {
    @apply bg-dark-200;
  }
  
  ::-webkit-scrollbar-thumb {
    @apply bg-dark-400 rounded-lg;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    @apply bg-dark-500;
  }
}

@layer components {
  .btn-primary {
    @apply px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-medium rounded-lg transition-colors duration-200;
  }
  
  .btn-secondary {
    @apply px-6 py-3 bg-transparent border border-dark-400 hover:border-blue-500 text-dark-800 hover:text-blue-400 font-medium rounded-lg transition-all duration-200;
  }
  
  .section-title {
    @apply text-2xl sm:text-3xl md:text-4xl font-bold text-white mb-6 sm:mb-8;
  }
  
  .card {
    @apply bg-gray-900/80 backdrop-blur-sm border border-gray-800/50 rounded-xl p-4 sm:p-6 shadow-2xl hover:shadow-cyan-500/20 hover:border-cyan-500/30 transition-all duration-300;
  }
  
  .navbar-fixed {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    width: 100% !important;
    z-index: 9999 !important;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
    will-change: transform;
  }
  
  /* Container responsive utilities */
  .container-responsive {
    @apply w-full max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }
}

@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Geometric Background Image - removed fixed attachment for better mobile performance */
.geometric-pattern-bg {
  background-image: url('./assets/geometric-background.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  /* Removed background-attachment: fixed for mobile compatibility */
}

/* Dark Geometric patterns - responsive sizing */
.geometric-bg {
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(79, 172, 254, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(236, 72, 153, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 75% 25%, rgba(34, 197, 94, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 25% 75%, rgba(251, 146, 60, 0.08) 0%, transparent 50%);
}

/* Dark Floating particles - responsive sizing */
.particles {
  position: relative;
  overflow: hidden;
}

.particles::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 20% 80%, rgba(79, 172, 254, 0.2) 1px, transparent 1px),
    radial-gradient(circle at 80% 20%, rgba(236, 72, 153, 0.2) 1px, transparent 1px),
    radial-gradient(circle at 40% 40%, rgba(34, 197, 94, 0.2) 1px, transparent 1px);
  background-size: 
    clamp(80px, 15vw, 120px) clamp(80px, 15vw, 120px), 
    clamp(60px, 10vw, 80px) clamp(60px, 10vw, 80px), 
    clamp(100px, 18vw, 140px) clamp(100px, 18vw, 140px);
  animation: float 25s linear infinite;
  opacity: 0.3;
}

@keyframes float {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  100% {
    transform: translateY(-6.25rem) rotate(360deg); /* -100px converted to rem */
  }
}

/* Responsive typography utilities */
@layer utilities {
  .text-responsive-xs {
    font-size: clamp(0.75rem, 2vw, 0.875rem);
  }
  
  .text-responsive-sm {
    font-size: clamp(0.875rem, 2.5vw, 1rem);
  }
  
  .text-responsive-base {
    font-size: clamp(1rem, 3vw, 1.125rem);
  }
  
  .text-responsive-lg {
    font-size: clamp(1.125rem, 3.5vw, 1.25rem);
  }
  
  .text-responsive-xl {
    font-size: clamp(1.25rem, 4vw, 1.5rem);
  }
  
  .text-responsive-2xl {
    font-size: clamp(1.5rem, 5vw, 2rem);
  }
  
  .text-responsive-3xl {
    font-size: clamp(1.875rem, 6vw, 2.5rem);
  }
  
  .text-responsive-4xl {
    font-size: clamp(2.25rem, 7vw, 3rem);
  }
}

 